@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,200;1,100;1,300&display=swap');


body {
  font-family: 'Josefin Sans', sans-serif;
  background-color: beige;
}

/* loading bar center align */
.center {
  width: 100%;
  height: 50vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* each movie box style */
.movieBox {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 100px;
}

a {
  text-decoration: none;
  color: #333;
}

.topTitle {
  padding-top:50px;
  margin-bottom: 0;
  text-align: center;
}

.movie {
  /* background-color: beige; */
  background-color: #00008B;
  background-color: #FFE77A;

  display: flex;
  width: 420px;
  border-radius: 18px;
  margin-bottom: 32px;
  box-shadow: 5px 5px 5px rgb(70, 69, 69);
  transition: .5s;
}

.movie:hover {
  /* background-color: rgb(149, 255, 0); */
  background-color: #ffbeb8;
  transform: scale(1.02);
}

.movie > div > img {
  max-width: 160px;
  padding: 20px 0 20px 20px;
}

.description {
  width: 100%;
  margin: 18px;
  position: relative;
}

.description > h3 {
  width: 200px;
  
  text-overflow: ellipsis; 
  white-space: nowrap;
  overflow: hidden;
}

.description div:last-child {
  margin-top: 5px;
}

.summary {
  font-size: 14px;
  max-height: 70px;
  max-width: 200px;
  overflow: scroll;
  border: 1px dotted rgb(99, 94, 92);
  border-radius: 8px;
  padding: 7px;
}

.summary::-webkit-scrollbar {
  display: none;
}

.movieInfos {
  font-size: 13px;
}
.genres {
  padding-left: 0;
  display: flex;
  margin-bottom: 5px;
  width: 200px;
  white-space: nowrap;
  text-overflow: ellipsis; 
  overflow: hidden;
}

li {
  list-style: none;
}

/* movie details */

.genre:not(:last-child)::after {
  content: ", \00a0"
}

.detailContainer {
    display: flex;
    justify-content: center;
  }

.detailBox {
    margin-top: 60px;
    background-color: #FFE77A;
    width: 800px;
    padding-top: 20px;
    box-shadow: 3px 3px 3px rgb(75, 73, 73);
    border-radius: 5px;
}


/* movie title */
.detailBox h1 {
    text-align: center;
    margin: 40px;
  }

.detailBox>div:nth-child(2) {
  text-align: center;
}

.detailBox>div>img {
  width: 85%;
  /* margin: 2.5%; */
  border-radius: 20px;
  box-shadow: 5px 5px 5px rgb(75, 73, 73);
}

.detailInfos {
  margin: 60px;
  font-size: 23px;
}